import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const NavStyle = styled.nav`
  display: flex;
  align-items: right;
  
  justify-content: center
  min-width: 0;
  font-family: Roboto;
  text-decoration: none;
  height: 80px;
  line-height: 80px;
  font-size: 24px;
  font-weight: 300;
`;

const TextNavWrapper = styled.span`
  padding: 20px;
`;

const Nav = () => (
  <NavStyle>
    <TextNavWrapper>
      <Link to="/">home</Link>
    </TextNavWrapper>
    <TextNavWrapper>
      <Link to="/projects">projects</Link>
    </TextNavWrapper>
  </NavStyle>
);

export default Nav;



