import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
    img.six {
      display: block;
      margin-right: auto
      margin-left: auto;
      height: auto;
      width: 80px;
      padding-top: 20px;
    }
    
      h1 {
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        font-family: 'Roboto', sans-serif;
        color: #d3d3d3;
      }
      p {
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        font-family: 'Roboto', sans-serif;
        color: #6cbce4;
        margin-right: 15%;
        margin-left: 15%;

      }

    `;

const Image = styled.img`
  width: 80%;
  max-width: 800px;
  display: block;
  height: auto;
  margin-right: auto;
  margin-left: auto;
`;

const Streetwise = () => (
  <Wrapper>
    <img className="six" src="6.png" alt="6" />
    <h1>UX Streetwise</h1>
    <p>
      Just a few ideas I had on the wireframing with an App idea called streetwise, allows the abilty for a community
      driven network to report differerent types of crime, allowing users to find the safest route home.
    </p>

    <Image src="../images/UX-artboard.jpg" alt="aark-greyscale" />
  </Wrapper>
);

export default Streetwise;



