import React from 'react';
import styled from 'styled-components';
import posed from 'react-pose';
import Project from './Project';
import data from './myprojects.json';

const Toplevel = styled.div`
  img.six {
    display: block;
    margin-right: auto
    margin-left: auto;
    height: auto;
    width: 80px;
    padding-top: 20px;
  }

  .something {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .something span {
    background-color: black;
    font-family: 'Roboto', sans-serif;
    color: #d3d3d3;

    font-weight: 200;
  }
`;

const AllProjects = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap
  height: 500px;
  width: 100%;
  
  justify-content: space-evenly;
`;

const Stagger = posed.div({
  exit: {
    x: '-100%'
  },
  enter: {
    x: '0%'
  }
});

const myData = data.projects;

const Projects = () => (
  <Toplevel>
    <div>
      <img className="six" src="6.png" alt="6" />
      <h2 className="something">
        <span>__projects</span>
      </h2>
      <AllProjects>
        {myData.map(project => (
          <div className="projectWrapper">
            <Stagger initialPose="exit" pose="enter">
              <Project key={project.id} {...project} />
            </Stagger>
          </div>
        ))}
      </AllProjects>
    </div>
  </Toplevel>
);

export default Projects;



