import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
img.six {
  display: block;
  margin-right: auto
  margin-left: auto;
  height: auto;
  width: 80px;
  padding-top: 20px;
}

  h1 {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    font-family: 'Roboto', sans-serif;
    color: #d3d3d3;
  }
  p {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    font-family: 'Roboto', sans-serif;
    color: #6cbce4;
    margin-right: 15%;
    margin-left: 15%;
  }
  iframe {
    width: 800px;
    height: 900px;
  }
`;

const AllProjects = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap
  height: 500px;
  width: 100%;
  
  justify-content: space-evenly;
`;

const MoonMelon = () => (
  <Wrapper>
    <img className="six" src="6.png" alt="6" />
    <h1>moonmelon</h1>
    <p>
      A new type of social network that allows uers to base there events and nights out around an app.(Still in dev){' '}
      <br />
      <a href="http://moonmelon.io">moonmelon.io</a>{' '}
    </p>
    <AllProjects>
      <iframe
        title="moonlight original idea"
        src="https://player.vimeo.com/video/181715114?color=4545cd"
        webkitallowfullscreen=""
        mozallowfullscreen=""
        allowFullscreen=""
        frameBorder="0"
      >
        Browser not compatible.
      </iframe>

      <iframe
        title="orignal moonmelon login idea"
        src="https://player.vimeo.com/video/177956193?color=4545cd"
        webkitallowfullscreen=""
        mozallowfullscreen=""
        allowFullscreen=""
        frameBorder="0"
      >
        Browser not compatible.
      </iframe>

      <img src="../images/moonmelon-moonlight.png" alt="moonmelon-moonlight" />
      <img src="../images/moonmelon-message.png" alt="moonmelon-message" />
      <img src="../images/moonmelon-post.png" alt="moonmelon-post" />
      <img src="../images/moonmelon-profile.png" alt="moonmelon-profile" />
    </AllProjects>
  </Wrapper>
);

export default MoonMelon;



