import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
    img.six {
      display: block;
      margin-right: auto
      margin-left: auto;
      height: auto;
      width: 80px;
      padding-top: 20px;
    }
    
      h1 {
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        font-family: 'Roboto', sans-serif;
        color: #d3d3d3;
      }
      p {
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        font-family: 'Roboto', sans-serif;
        color: #6cbce4;
      }

    `;

const AllProjects = styled.div`
      display: flex;
      flex-direction: row;
      flex-wrap: wrap
      
      width: 100%;
      
      justify-content: space-evenly;
    `;

const styleForDiv = {
  border: 'none',
  width: '100%',
  height: '100%'
};

const styleForIframe = {
  width: '60%',
  height: '500px'
};

const Warming = () => (
  <Wrapper>
    <img className="six" src="6.png" alt="6" />
    <h1>Global Warming editorial</h1>

    <p>A graphic focused editorial to raise awareness about global warming.</p>
    <AllProjects>
      <div data-configid="15740381/37234266" style={styleForIframe} className="issuuembed issuu-isrendered">
        <iframe
          src="https://e.issuu.com/embed.html?identifier=pbw4jhn5tkq5&amp;hostUrl=http%3A%2F%2Freqticorp.com%2Fportfolio%2Fglobal-warming.html&amp;hostReferrer=http%3A%2F%2Freqticorp.com%2Fportfolio-essex-web-design.html&amp;embedType=script#15740381/37234266"
          style={styleForDiv}
          title="issuu.com"
          allowFullscreen=""
          webkitallowfullscreen=""
          mozallowfullscreen=""
          msallowfullscree=""
        />
      </div>
    </AllProjects>
  </Wrapper>
);

export default Warming;



