import React, { Component } from 'react';
import styled from 'styled-components';
import ProjectButton from './Button';

const Toplevel = styled.div`
  .six {
    height: auto;
    width: 80px;
    margin-right: auto;
    margin-left: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .header {
    position: relative;
    padding-top: 30vh;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
  }

  .something {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    color: #d3d3d3;

    font-weight: 200;
  }
  .something span {
    color: #6cbce4;
    background-color: black;
  }
`;

export default class Home extends Component {
  state = {};

  componentDidMount() {}

  render() {
    return (
      <Toplevel>
        <div className="header">
          <img className="six" src="6.png" alt="6" />
          <h1 className="something">
            <span>studio six</span>
          </h1>
          <h2 className="something">
            <span>Front end Developer based in London</span>
          </h2>

          <ProjectButton name="View Projects" link="/projects" />
        </div>
      </Toplevel>
    );
  }
}



