import React from 'react';
import { Route, Switch } from 'react-router-dom';
import Home from './Home';
import Nav from './Nav';
import Projects from './Projects';
import MoonMelon from './MoonMelon';
import Aark from './Aark';
import Warming from './Warming';
import Streetwise from './Streetwise';
// const FourOhFour = () => <h1>404</h1>;

const App = () => (
  <div className="App">
    <Nav />
    <Switch>
      <Route exact path="/" component={Home} />
      <Route path="/projects" component={Projects} />
      <Route path="/moonmelon" component={MoonMelon} />
      <Route path="/aark" component={Aark} />
      <Route path="/globalwarming" component={Warming} />
      <Route path="/streetwise" component={Streetwise} />
    </Switch>
  </div>
);
export default App;



